.container {
  width: 1440px;
  margin-right: auto;
  margin-left: auto;

  @media (max-width: $desktop-min-width) {
    width: 800px;
  }

  @media (max-width: $tablet-middle) {
    width: 700px;
  }

  @media (max-width: $tablet-min-width) {
    width: 580px;
  }

  @media (max-width: $mobile-max-width) {
    width: auto;
  }
}

.page-title {
  font-family: $oswald-medium;
  margin-bottom: 40px;
  font-size: 48px;
  color: #ffffff;
  text-align: center;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.21);

  @media (max-width: $mobile-max-width) {
    margin-top: 0;
    font-size: 33px;
  }
}

.registration-form {
  box-shadow: 0 0 30px 10px rgba(0, 0, 0, 0.1);
  width: 800px;
  margin-right: auto;
  margin-left: auto;
  padding: 40px;
  border: 1px solid #aaaaaa;
  border-radius: 10px;
  background-color: #ffffff;

  @media (max-width: $tablet-middle) {
    width: 600px;
  }

  @media (max-width: $tablet-min-width) {
    width: 560px;
  }

  @media (max-width: $mobile-max-width) {
    width: 90%;
    padding: 15px;
  }

  .top,
  .middle,
  .bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .bottom {
    margin-top: 20px;
  }
}

.input-wrap {
  display: flex;
  flex-direction: column;
  width: calc(50% - 20px);
  margin-bottom: 20px;

  @media (max-width: $mobile-max-width) {
    width: 100%;
  }
}

.input-label {
  display: block;
  margin-bottom: 5px;
  font-size: 14px;
  opacity: .6;
}

.input {
  padding: 10px;
  border: 1px solid #9f9f9f;
  border-radius: 4px;
  transition: .2s ease;
  outline: none;

  &:hover,
  &:focus {
    border-color: #00a9d3;
  }
}

.select {
  padding: 12px 10px;
}

.button {
  width: calc(50% - 20px);
  padding: 20px;
  border: none;
  border-radius: 4px;
  font-size: 20px;
  color: #ffffff;
  text-transform: uppercase;
  cursor: pointer;
  transition: .2s ease;
  outline: none;

  @media (max-width: $mobile-max-width) {
    width: calc(50% - 10px);
    padding: 15px;
    font-size: 16px;
  }
}

.reset {
  background-color: #1f2d2d;

  &:hover,
  &:focus {
    background-color: #2f4646;
  }

  &:active {
    color: rgba(255, 255, 255, 0.6);
  }
}

.submit {
  background-color: #86331f;

  &:hover,
  &:focus {
    background-color: #9e1e00;
  }

  &:active {
    color: rgba(255, 255, 255, 0.6);
  }
}
